import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
const store =  new Vuex.Store({
  strict: true,
    state:{
      count: 0,
      a: 12,
      b: 5,
      userline:[]
    },
    mutations:{
      increment(state, n){
        // console.log(state);
        state.count+=n
      },
      reduce(state, n){
        state.count-=n
      },
      setA(state, a){
        state.a = a
      },
      setB(state, b){
        state.b = b
      },
      setOnline( state, id){
        state.userline.forEach(user=>{
          if(user.id == id){
            user.state = true
          }
        })
      },
      setUsers(state, users){
        state.userline = users
       
      }

    },
    
    actions:{
      f1(context, n){
         context.commit('increment', n)
      },
      f2({commit}, n){
         commit('reduce', n)
      },
      setA({commit}, a){
        commit('setA', a)
      },
      setB({commit}, b){
        commit('setB', b)
      },
      setOnline(ctx, id){
        ctx.commit('setOnline', id)
        console.log(ctx);
      },
      async readUsers({commit}){
        let res = await fetch('http://localhost:8081/data.txt')
        let user = await res.json()
        commit('setUsers', user)
      }
    },
    getters:{
      getCount(state){
        return state.a + state.count 
      },
      setOnlines(state){
        return state.userline.filter(user=>user.state)
      }
    },
    modules:{
      mod_a:{
        state:{
          a: 1
        },
        mutations:{
          setA(state, num){
            state.a = num
          }
        }
      }
    }
 })

 export default store


