<template>
  <div class="index">
    <p>a:{{a}}</p>
    <!-- <p>b:{{b}}</p> -->
    mod_a:{{mod_a}}
    <p>{{ getCount}}</p>
    <p>count: {{ $store.getters.getCount}}</p>
    <ul>
      <li v-for="(item, index) in setOnlines" :key="index">
        姓名：{{item.name}} 年龄：{{ item.age }}
      </li>
    </ul>
    <button @click="setOnline(2)">召唤biubiu</button>
    <!-- <p>{{ count }}</p> -->
    <button @click="count+=5">count</button>
    <button @click="fn(1)">+1</button>
    <button @click="fn(20)">actions</button>
    <button >getters</button>
  </div>
</template>
<script>
import { mapState, mapActions, mapGetters } from 'vuex'
  export default {
    methods: {
      add(count){
        this.$store.commit('increment',count)
      },
      // f1(n){
      //   this.$store.dispatch('f1', n)
      // },
      ...mapActions(['setOnline','readUsers']),
      ...mapActions({
        fn: 'f1'
      })
    },
    async created(){
      // await this.readUsers()
      // console.log(this.$store.state.userline);
      
      this.$store.commit('setA', 5)
      console.log(this.$store.state.a);
      console.log(this.$store.state.mod_a.a);
    },
    computed:{
      ...mapState(['a','b']),
      ...mapGetters(['getCount', 'setOnlines']),
      ...mapState({
        mod_a: state=>state.mod_a.num
      })
      // count:{
      //   get(){
      //     return this.$store.getters.getCount
      //   },
      //   set(value){
      //     this.$store.dispatch('setA', value-5)
      //     this.$store.dispatch('setB', 5)
      // // }
      



    }
  }
</script>
<style lang="">
  
</style>


