<template>
  <div>
    {{ $store.state.count}}<br>
    <button @click="reduce(10)">-1</button>
    <button @click="f2(20)">actions</button>
  </div>
</template>
<script>
export default {
  methods:{
    reduce(n){
      this.$store.commit('reduce',n)
    },
    f2(n){
      this.$store.dispatch('f2', n)
    } 
  }
}
</script>


