import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index.vue'
import Cale from '@/components/cale'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },{
      path: '/cale',
      name: 'cale',
      component: Cale
    }
  ]
})



